 * 1. Add the correct display in Edge, IE, and Firefox.
 */
details,
menu {
  display: block; }

/*
 * Add the correct display in all browsers.
 */
summary {
  display: list-item; }

/* Scripting
   ========================================================================== */
/**
 * Add the correct display in IE 9-.
 */
canvas {
  display: inline-block; }

/**
 * Add the correct display in IE.
 */
template {
  display: none; }

/* Hidden
   ========================================================================== */
/**
 * Add the correct display in IE 10-.
 */
[hidden] {
  display: none; }

/**
* Utility classes
*/
.col-padding {
  padding: 0 20px;
  padding: 0 2rem; }
  @media (min-width: 1024px) {
    .col-padding {
      padding: 0 30px;
      padding: 0 3rem; } }

/**
* Wordpress image alignment
*/
.alignright {
  float: none; }
  @media (min-width: 1024px) {
    .alignright {
      float: right; } }

.alignleft {
  float: none; }
  @media (min-width: 1024px) {
    .alignleft {
      float: left; } }

.aligncenter {
  margin: 0 auto;
  max-width: 100%; }

*,
*:before,
*:after {
  box-sizing: border-box;
  -webkit-font-smoothing: antialiased;
  -webkit-backface-visibility: hidden; }

::-moz-selection,
::selection {
  background: #0072bc;
  color: #fff; }

html {
  font-size: 62.5%; }

body {
  background: #f1f1f1;
  color: #212121;
  font-family: "Overpass", sans-serif;
  font-weight: 300;
  font-size: 16px;
  font-size: 1.6rem;
  line-height: 1.3; }

.row {
  max-width: 1200px;
  max-width: 120rem;
  margin-left: auto;
  margin-right: auto; }
  .row:before, .row:after {
    content: ' ';
    display: table; }

.icon-link {
  display: inline-block; }
  .icon-link .arrow--left {
