    height: 40px;
    height: 4rem;
    margin: 0;
    width: 70px;
    width: 7rem; }
  .icon-link .arrow-path {
    fill: #212121;
    transition: all 300ms ease; }
  .icon-link:hover .arrow-path, .icon-link:focus .arrow-path {
    fill: #0072bc; }

h1, .page-title, .h1 {
  font-family: "Playfair Display", serif;
  font-weight: 900;
  line-height: 1.2;
  margin: 0 0 15px 0; }

h2, .h2,
h3, .h3 {
