  font-weight: 700;
  line-height: 1.2;
  margin: 0 0 20px 0; }

h4, .h4,
h5, .h5,
h6, .h6 {
  font-family: "Overpass", sans-serif;
  font-weight: 600;
  margin: 0 0 15px 0; }

strong {
  font-weight: 600; }

.page-title {
  font-size: 32px; }
  @media (min-width: 1024px) {
    .page-title {
      font-size: 55px;
      font-size: 5.5rem; } }

.page-summary {
  font-size: 17px;
  font-size: 1.7rem; }
  @media (min-width: 1024px) {
    .page-summary {
      font-size: 20px;
      font-size: 2rem; } }

.page-header {
  padding: 20px;
  padding: 2rem; }

.page-content {
  background: #fff;
  padding-bottom: 20px;
  padding-bottom: 2rem; }
  @media (min-width: 1024px) {
    .page-content {
      padding-bottom: 60px;
      padding-bottom: 6rem; } }

.layout--interior {
  padding-top: 60px;
  padding-top: 6rem; }

.flex-row {
  align-items: stretch;
  display: flex;
  flex-wrap: wrap; }

.flex-col {
  width: 100%; }
  @media (min-width: 1024px) {
    .flex-col {
      width: 50%; } }

@media (min-width: 1024px) {
  .flex-reorder {
    order: 1; } }

@media (min-width: 1024px) {
  .flex-reorder:nth-of-type {
    order: 2; } }

.flex-video {
  height: 0;
  margin-bottom: 30px;
  margin-bottom: 3rem;
  padding-bottom: 54%;
  position: relative;
  overflow: hidden; }
  @media (min-width: 1024px) {
    .flex-video {
