      margin-bottom: 6rem; } }
  .flex-video.video--vimeo {
    padding-bottom: 57%; }
  .flex-video iframe,
  .flex-video video {
    height: 100%;
    left: 0;
    position: absolute;
    top: 0;
    width: 100%; }

.featured-media {
  margin-left: auto;
  margin-right: auto;
  max-width: 1000px;
  max-width: 100rem;
  margin-top: -55px;
  margin-top: -5.5rem;
  padding: 0 10px;
  padding: 0 1rem; }
  @media (min-width: 1024px) {
    .featured-media {
      margin-top: -75px;
      margin-top: -7.5rem; } }

figure {
  margin: 0 0 20px 0;
  margin: 0 0 2rem 0;
  padding: 0;
  position: relative; }

img {
  display: inline-block;
  max-width: 100%;
  width: auto; }

.browser {
  background: #212121;
  border-radius: 5px 5px 0 0;
  box-shadow: 0 3px 10px rgba(0, 0, 0, 0.5);
  padding: 0;
  transition: all ease 250ms;
  width: 100%; }

.browser-topbar {
  align-items: center;
  display: flex;
  justify-content: flex-start;
  padding: 10px 10px 5px;
  padding: 1rem 1rem 0.5rem;
  position: relative; }

.browser-btn {
  background: #212121;
  border: solid 1px #212121;
  border-radius: 50%;
  height: 13px;
  display: inline-block;
  margin: 0 3px;
  transition: 300ms background ease;
  width: 13px; }
  .browser-btn:hover, .browser-btn:focus {
    opacity: 0.5; }
  .browser-btn:nth-of-type(1) {
    background: #ff3434;
    border-color: #cd0000; }
  .browser-btn:nth-of-type(2) {
    background: #ffc100;
    border-color: #997400; }
  .browser-btn:nth-of-type(3) {
    background: #67b678;
    border-color: #3b7c49; }

.address-bar {
  background: rgba(255, 255, 255, 0.9);
  border: solid thin #212121;
  border-radius: 3px;
  flex-grow: 1;
  font-size: 14px;
  margin-left: 10px;
  padding: 2px 8px; }
  .address-bar_url {
    color: #212121;
    text-decoration: none;
    vertical-align: middle; }
    .address-bar_url:hover, .address-bar_url:focus {
      color: #0072bc; }

.cta {
  background: #0072bc center fixed no-repeat;
  background-size: cover;
  margin: 40px 0;
  margin: 4rem 0;
  padding: 40px 20px;
  padding: 4rem 2rem;
  position: relative;
  text-align: center; }
  @media (min-width: 1024px) {
    .cta {
      margin: 40px 0 60px;
      margin: 4rem 0 6rem;
      padding: 40px 20px;
      padding: 4rem 2rem; } }
  .cta .row {
    position: relative;
    z-index: 1; }
  .cta::after {
    background: rgba(33, 33, 33, 0.1);
    content: '';
    height: 100%;
    left: 0;
    position: absolute;
    top: 0;
    width: 100%;
    z-index: 0; }

.cta-text {
  color: #fff;
  font-family: "Overpass", sans-serif;
  font-size: 18px;
  font-size: 1.8rem;
  font-weight: 600;
  text-decoration: none;
