.unstyled-list {
  margin: 0;
  padding: 0; }
  .unstyled-list li {
    list-style: none; }

.swatches {
  align-items: stretch;
  display: flex;
  justify-content: space-between;
  margin: 20px 0;
  margin: 2rem 0;
  width: 100%; }
  @media (min-width: 1024px) {
    .swatches {
      margin: 17px 0 30px 0;
      margin: 1.7rem 0 3rem 0; } }

.swatch {
  height: 8px;
  height: 0.8rem;
  width: 100%; }

.tags-label {
  background: #3b3b3b;
  border-radius: 3px;
  color: #fff;
  display: inline-block;
  font-weight: 600;
  font-size: 13px;
  letter-spacing: 1px;
  padding: 5px 10px;
  text-transform: capitalize; }
